<script>
export default {
  props: ['stock', 'price', 'brandList', 'lstAttributes', 'location'],
  data() {
    return {
      fold_price: true, // 价格折叠
      fold_brand: true, // 品牌折叠
      lowestPrice: '',
      highestPrice: '',
    }
  },
  watch: {
    price(newValue) {
      if (newValue === '' || newValue === '0') {
        this.lowestPrice = ''
        this.highestPrice = ''
      } else {
        this.lowestPrice = newValue.split(';')[0] === 'a' ? '' : newValue.split(';')[0]
        this.highestPrice = newValue.split(';')[1] === 'a' ? '' : newValue.split(';')[1]
      }
    },
  },
  methods: {
    fnShowGoodsFilter() {
      this.$emit('fnShowGoodsFilter')
    },
    fnFold(type) {
      if (type === 'price') {
        this.fold_price = !this.fold_price
      } else if (type === 'brand') {
        this.fold_brand = !this.fold_brand
      } else {
        this.$emit('fnFold', type)
      }
    },
    fnSelect(type, index, subIndex) {
      this.$emit('fnSelect', {
        type: type,
        index: index,
        subIndex: subIndex,
      })
    },
    fnReset() {
      this.lowestPrice = ''
      this.highestPrice = ''
      this.$emit('fnReset')
    },
    fnConfirm() {
      // 处理价格 价格最低、最高分号分隔，都不填只传一个0，填一个情况下不填的部分空例如只填最低价100;
      let _price
      if (this.lowestPrice && this.highestPrice) {
        if (+this.lowestPrice > +this.highestPrice) {
          this.$toast('最低价不能高于最高价')
          return
        }
      }
      if (!this.lowestPrice && !this.highestPrice) {
        _price = '0'
      } else {
        _price = `${this.lowestPrice ? this.lowestPrice : 'a'};${this.highestPrice ? this.highestPrice : 'a'}`
      }
      this.$emit('fnConfirm', {
        price: _price,
      })
      this.fnShowGoodsFilter()
    },
    // 切换地址
    changePosition() {
      this.$emit('changeAddr')
    }
  },
}
</script>

